.landing {}

.landing .page {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin-top: 80px;
    margin-bottom: 60px;
}


.landing h1 {
    font-size: 96px;
    font-family: 'Raleway' !important;
    font-weight: 900 !important;
    color: #FFF;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1em;
}

.landing h1 span {
    color: var(--my-pink);
}

.landing p {
    color: white;
    line-height: 2em;
}

.landing button {
    margin-top: 50px;
    margin-bottom: 40px;
    gap: 6px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    padding: 1em 2em;
    border: none;
    color: white;
    font-weight: bold;
    font-size: var(--my-medium-font);
    background: linear-gradient(105deg, #AA07E0 36%, #F80282 90%, #FD309B 100%);
}


.landing .right {
    position: relative;
}



.landing .bot {
    width: 100%;
}

.landing .gradient {
    position: absolute;
    right: 10vw;
    top: -10vh;
    z-index: -1;
    opacity: 0.9;
}

.landing .phone{
    display: none;
}




@media only screen and (max-width: 1100px) {
    .landing h1 {
        font-size: 60px;
    }

    .landing .gradient {
        top: -15vh;
    }

}

@media only screen and (max-width: 900px){
    /* .landing .gradient {
        bottom: -100px;
    } */
}

@media only screen and (max-width: 800px) {
    .landing .gradient {
        display: none;
    }
    .landing .page {
        grid-template-columns: 100%;

    }

    .landing .phone{
        display: grid;
    }

    .landing .desktop{
        display: none;
    }

    .landing .gradient {
        bottom: 0px;
        opacity: 0.5;

    }

}




@media only screen and (max-width: 700px) {
    .landing .right {
        grid-template-columns: 2fr 1fr;
    }
}




@media only screen and (max-width: 550px) {
    .landing .gradient {
        bottom: -50px;
    }
}