footer {
    background-color: #181924;
    min-height: 300px;
}

footer .page{
    padding-top: 60px;
    padding-bottom: 60px;
}


footer .logo{
    display: block;
    margin: auto;
    height: 60px;
}

footer .links{
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 50px;
}


footer .links a{
    color: rgba(255, 255, 255, 0.733);
    text-transform: uppercase;
}

footer .links a:hover{
    color: white;
}



footer .socials{
    display: flex;
    gap: 24px;
    align-items: center;
    justify-content: center;
}

footer .socials>*:hover{
    opacity: 0.7;
}


footer .copyright{
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    gap: 0.5em;
    background-color: black;
    padding-left: 16px;
    padding-right: 16px;
}

footer .copyright p{
    margin: 0;
    text-align: center;
}


footer .copyright p.version{
    color: rgba(255, 255, 255, 0.562);
}





@media only screen and (max-width: 800px){
    footer .links{
        gap: 24px;
    }
}