header {
    background-color: var(--my-color-surface);
    
}

header .page {
    display: grid;
    grid-template-columns: max-content auto max-content;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}

header .center a {
    color: rgba(255, 255, 255, 0.685);
}

header .center a.active,
header .center a:hover {
    color: white;
}


header .logo {
    height: 40px;
}

header .center {
    display: flex;
    gap: 16px;
}

header button {
    padding: 1em 2em;
    border-radius: 50px;
    border: none;
}

header button.login{
    background-color: var(--my-primary);
}



header button.simple{
    background-color: white;
}


header button.simple a {
    color: rgb(56, 56, 56);
    display: flex;
    align-items: center;
    gap: 16px;
}


header button.login a {
    color: white;
}



@media only screen and (max-width: 850px) {
    header .center {
        display: none;
    }

    header .page {
        grid-template-columns: max-content max-content;
        height: 80px;
    }
}