.feature-section {
    margin-bottom: 100px;
    position: relative;
}


.feature-section .page {
    margin-bottom: 100px;
}

.feature-section .page.first {
    padding-top: 150px;
}

.feature-section .page.last {
    padding-bottom: 100px;
}



.feature-section .page>div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    align-items: center;
    color: white;
}

.feature-section img {
    width: 100%;
}

.feature-section h2 {
    font-size: 56px;
    margin: 1em 0;
    font-weight: 900;
}

.feature-section p {
    font-size: 20px;
    margin: 0.6em 0;
}


.feature-section h2 span {
    color: var(--my-pink);
}

.feature-section .content div {
    display: flex;
    align-items: center;
    gap: 1em;
    line-height: 1.6em;
}

.feature-section .content div::before {
    content: url('../../assets/Icons/check.svg');

}

.feature-section .content.dot div::before {
    content: url('../../assets/dot2.png');

}


@media only screen and (max-width: 1100px) {
    .feature-section h2 {
        font-size: 40px;
    }
}


@media only screen and (max-width: 800px) {
    /* .feature-section .page>div {
        grid-template-columns: 100%;
        gap: 0;
    } */

    .feature-section .page>div.center {
        display: flex;
        flex-direction: column-reverse;
        align-items: unset;
    }

    .feature-section .page>div.bottom {
        display: flex;
        flex-direction: column;
        align-items: unset;
    }

    .feature-section {
        margin-top: 100px;
    }
}