/* chat area */

.chat-box.light {
    --my-color-surface2: white;
    --my-color-surface3: rgb(240, 240, 240);
    --my-color-surface: rgb(240, 240, 240);
    --my-power-color: rgb(143, 143, 143);
}

.chat-box {
    --my-power-color: rgb(172, 172, 172);
}

.chat-box.light .header button {
    fill: black;
}


.chat-box.light .chat p {
    background-color: #F4F4F5;
}

.chat-box.light .default button {
    background-color: #F4F4F5;
    color: black;
}


.chat-box.light form {
    background-color: #F4F4F5;
}

.chat-box.light input {
    color: black;

}

.chat-box.light form button {
    fill: rgb(82, 82, 82);
}














.chat-box .header .logo {
    height: 32px;
}


.chat-box .header button svg {
    width: 20px;
    height: 20px;
    fill: inherit;
}

.chat-box form button img {
    height: 20px;
}



.chat-box {
    background-color: var(--my-color-surface2);
    display: grid;
    grid-template-rows: max-content auto max-content max-content;
    height: 100vh;
    max-height: 700px;
    overflow: auto;
    border: 1px solid rgba(255, 255, 255, 0.14);

}

.chat-box .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--my-color-surface3);
    margin-bottom: 0;
    margin: 0 !important;
    height: 60px;
    padding: 0 24px;
}


.chat-box .header div{
    display: flex;
    align-items: center;
    gap: 8px;
}

.chat-box .header div img{
    height: 40px;
}

.chat-box .header div span{
    color: #BDBEC4;
    font-weight: 900;
    font-size: 18px;
}


.chat-box .header button {
    background-color: transparent;
    border: none;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: var(--my-color-surface);
    display: flex;
    align-items: center;
    justify-content: center;
    fill: white;
}




.chat-box .box-content {
    display: flex;
    flex-direction: column;
    height: auto;
    min-height: 100px;
    overflow-y: auto;
    gap: 8px;
    padding: 50px 24px;
    scroll-behavior: smooth;

}



.chat-box .default::-webkit-scrollbar {
    height: 3px;
}

.chat-box .box-content::-webkit-scrollbar {
    width: 6px;
}

/* Track */
.chat-box .box-content::-webkit-scrollbar-track,
.chat-box .default::-webkit-scrollbar-track {
    background: transparent;
}

/* Handle */
.chat-box .box-content::-webkit-scrollbar-thumb,
.chat-box .default::-webkit-scrollbar-thumb {
    background: #717280;
}

/* Handle on hover */
.chat-box .box-content::-webkit-scrollbar-thumb:hover,
.chat-box .default::-webkit-scrollbar-thumb:hover {
    background: #555;
}












.chat-box .chat {
    margin-right: 15%;
}

.chat-box .chat.primary {
    margin-right: 0;
    margin-left: 15%;
    display: flex;
    justify-content: end;
}

.chat-box .chat p {
    display: inline-block;
    padding: 0.6em 1em;
    border-radius: var(--my-border-radius);
    background-color: white;
    color: black;
    line-height: 1.5em;
    font-size: var(--my-medium-font);
    margin: 0;
    transition: all 200ms;
}

.chat-box .chat.primary p {
    background-color: var(--my-primary);
    color: white;
}


.chat-box .lead{
    
}















.chat-box .chat-footer {
    border-top: 1px solid rgba(179, 179, 179, 0.315);
    border-top: none !important;
    padding: 24px;
    padding-bottom: 0 !important;
    overflow-x: auto;
    width: 100%;
    /* background-color: #555; */
}




.chat-box .default {
    display: flex;
    gap: 8px;
    flex-wrap: nowrap;
    white-space: nowrap;
    overflow-x: auto;
}


.chat-box .default button {
    border: none;
    background-color: #27293C;
    border-radius: var(--my-border-radius);
    padding: 0.7em 1em;
    color: white;
    font-size: var(--my-medium-font);
    /* white-space: pre;
    display: block; */
}


.chat-box form {
    margin-top: 16px;
    display: grid;
    grid-template-columns: 1fr max-content max-content;
    align-items: center;
    background-color: black;
    border-radius: var(--my-border-radius);
    gap: 16px;
}

.chat-box input {
    padding: 1em 1.4em;
    background-color: transparent !important;
    border: none !important;
    color: white;
    display: block;
    outline: none !important;
    font-size: var(--my-medium-font);
}


.chat-box>hr {
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.144);
}

.chat-box .chat-footer hr {
    width: 1px;
    border: none;
    height: 24px;
    background-color: rgba(255, 255, 255, 0.295);
}

.chat-box form button {
    background-color: transparent;
    border: none;
    padding-right: 1em;
    padding-left: 1em;
    fill: white;
    transition: all 100ms;
}

.chat-box form button:hover{
    transform: scale(1.2);
}

.chat-box form button svg {
    width: 20px;
    height: 20px;
    fill: inherit;
}

.chat-box .power{
    font-size: 12px;
    text-align: center;
    color: var(--my-power-color);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
}

.chat-box .power a { 
    font-weight: bold;
    color: inherit;
    text-decoration: none;
}





/* light mode */






/* dot progress */

.progress-load {
    display: flex;
    border-radius: 50%;
    flex-direction: row;
    gap: 4px;
}

.progress-load .item {
    font-size: var(--my-medium-font);
    width: 1em;
    height: 1em;
    scale: 0;
    border-radius: 50%;
    background: rgb(211, 211, 211);
    animation: loading_492 1s ease infinite;
    animation-delay: 0.5s;
}

.progress-load.dark .item {
    background: black;

}

@keyframes loading_492 {
    50% {
        scale: 1;
    }
}


.progress-load .item:nth-child(1) {
    animation-delay: 0.3s;
}

.progress-load .item:nth-child(2) {
    animation-delay: 0.6s;
}

.progress-load .item:nth-child(3) {
    animation-delay: 0.9s;
}








@media only screen and (max-width: 900px) {
    .chat-box .header {
        height: 80px;
        padding: 0 40px;
    }


    .chat-box .box-content {
        padding: 60px 40px;
    }

    .chat-box .chat-footer {
        padding: 40px;
    }

}

@media only screen and (max-width: 700px) {
    .chat-box .header {
        height: 60px;
        padding: 0 12px;
    }

    .chat-box .header .logo {
        height: 32px;
    }


    .chat-box .box-content {
        padding: 50px 24px;
    }

    .chat-box .chat-footer {
        padding: 12px;
    }

    .chat-box .header button {
        width: 32px;
        height: 32px;
    }

    .chat-box .header button img {
        width: 20px;
        height: 20px;
    }

    .chat-box form button img {
        height: 20px;
    }

    .chat-box form {
        gap: 0;
    }
}