:root {
  --my-primary: #05668d;
  --my-color-on-primary: white;
  --my-secondary: red;
  --my-color-on-secondary: red;
  --my-color-surface: #000000;
  --my-color-surface2: #1f202f;
  --my-color-surface3: #2b2d42;
  --my-color-on-surface: #4e5460;
  --my-pink: #AA07E0;

  --my-medium-font: 16px;
  --my-small-font: 14px;
  --my-border-radius: 10px;
  --my-big-font: 32px;
  --my-average-font: 24px;
  --skeleton-color: rgba(255, 255, 255, 0.04);
  --page-gap: 24px;
  --max-page-width: 1280px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: var(--my-color-surface);
}

.page a {
  text-decoration: none;
}

.page {

  --max-dd: max(0px, ((100% - var(--max-page-width)) * 0.5) - var(--page-gap) * 2);
  margin: 0 calc(var(--max-dd));
  color: var(--my-color-on-surface);
  font-family: "Raleway";

}

.page>* {
  margin-left: var(--page-gap);
  margin-right: var(--page-gap);
}

.page h1 {
  color: var(--my-color-on-primary);
}



.page textarea {
  font-family: "Raleway";
}

.page .hover:hover {
  opacity: 0.7;
}


.page h2 {
  font-size: var(--my-big-font);
  margin-top: 1em;
  margin-bottom: 1em;
  font-weight: normal;
}

.page h3 {
  font-size: var(--my-average-font);
  margin-top: 1em;
  margin-bottom: 1em;
  font-weight: normal;
}

.page a,
.page p {
  font-size: var(--my-medium-font);
}

/* .page p {
  font-size: var(--my-medium-font);
  margin: 1em 0;
  line-height: 1.6em;
} */

/* Hide scrollbar for Chrome, Safari and Opera */
.hide-scroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hide-scroll {
  scroll-behavior: smooth;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}


.page .loading {
  width: 30px;
  height: 30px;
  border: 2px solid #FFF;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.page .loading.dark {
  border-color: #1f202f;
  border-bottom-color: transparent;
}

.page .loading.mini{
  width: 18px;
  height: 18px;
}

.page .loading.small {
  width: 24px;
  height: 24px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 800px) {
  :root {
    --my-medium-font: 14px;
    --my-border-radius: 8px;
    --my-small-font: 12px;
  }
}

@media only screen and (max-width: 700px) {
  :root {
    --page-gap: 16px;
  }
}





/* skeleton */


.skeleton {
  background-color: var(--skeleton-color);
  background: linear-gradient(90deg,
      rgba(255, 255, 255, 0) 40%,
      var(--my-color-surface3) 50%,
      rgba(255, 255, 255, 0) 60%) var(--skeleton-color);
  background-size: 200% 100%;
  background-position-x: 180%;
  animation: 1.5s skeleton ease-in-out infinite;
  margin: 16px 0;
}


.card.skeleton {
  width: 100%;
  max-width: 220px;
  min-height: 276px;
}


.heading.skeleton {
  height: 60px;
  width: 100%;
  max-width: 250px;
}

.text.skeleton {
  height: 24px;
  width: 100%;
}

@keyframes skeleton {
  to {
    background-position-x: -20%;
  }
}

















/* home page */
.home-chat {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
}

.home-chat h2 {
  color: white;
}

.home-chat-text {
  text-align: center;
  color: rgba(255, 255, 255, 0.822);
}


.grow-business .page {
  margin-top: 100px;
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  align-items: center;
}

.grow-business .page>img{
  width: 100%;
  margin: 0 !important;
}


.grow-business .page button {
  margin-top: 50px;
  margin-bottom: 40px;
  gap: 6px;
  display: flex;
  align-items: center;
  border-radius: 4px;
  padding: 1em 2em;
  border: none;
  color: white;
  font-weight: bold;
  font-size: var(--my-medium-font);
  background: linear-gradient(105deg, #AA07E0 36%, #F80282 90%, #FD309B 100%);
}

.grow-business h2{
  font-size: 60px;
  margin: 1em 0;
  font-weight: 900;
  color: white;
}


.grow-business h2 span{
  color: var(--my-pink);
}



@media only screen and (max-width: 1100px){
  .grow-business h2{
    font-size: 45px;
  }
  
}







@media only screen and (max-width: 900px){
  .grow-business h2{
    font-size: 32px;
  }
}

@media only screen and (max-width: 700px){
  .grow-business .page {
    grid-template-columns: 100%;

  }
}





















.auto-popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(240, 248, 255, 0.05); */
  /* pointer-events: none; */
  display: flex;
  justify-content: center;
  align-items: flex-end;
  pointer-events: none;
  z-index: 10;
}


.auto-popup button {
  position: sticky;
  bottom: 32px;
  /* align-items: center;
  justify-content: center; */
  border: none;
  height: max-content;
  font-size: var(--my-medium-font);
  background-color: transparent;
  pointer-events: auto;
}

.auto-popup button>a {
  position: relative;
  padding: 8px;
  height: 56px;
  min-width: 56px;
  overflow: hidden;
  display: flex;
  align-items: center;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(10px);
  color: white;
  scale: 0;
  transition: scale 200ms;
  text-decoration: none;

}


.auto-popup button>a.animate {
  animation-name: popup-start;
  animation-duration: 1.2s;
  scale: 1;
}

.auto-popup button>a.animate-rev {
  animation-name: popup-end;
  animation-duration: 1.2s;
}


@keyframes popup-start {
  0% {
      color: transparent;
      scale: 0;
      width: 56px;
  }

  50% {
      color: transparent;
      scale: 1;
      width: 56px;
  }

  100% {
      color: white;
      scale: 1;
      width: 236px;
  }
}

@keyframes popup-end {
  0% {
      scale: 1;
      width: 224px;
  }

  50% {
      scale: 1;
      width: 56px;
  }

  100% {
      scale: 0;
      width: 56px;
  }
}


.auto-popup span {
  padding-left: 20px;
  padding-right: 60px;
  white-space: pre;
}

.auto-popup button .icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #0071e3;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8px;
  right: 8px;
}
